<template>
  <div class="main">
    <div class="wrapper">
      <nav class="nav">
        <div class="logo">xme<span style="color: #939393;">di</span><span style="color: #bdbdbd;">a</span></div>
        <a class="burger-nav" id="drawer">&#9776;</a>
        <ul>
          <li class="right"><a v-on:click="logout()" href="">Logout</a></li>
          <li class="right"><div class="vertical_hr"></div></li>
          <li class="right"><router-link class="active" to="/tvshows">TV shows</router-link></li>
          <li class="right"><router-link to="/movies">Movies</router-link></li>
          <li class="right"><router-link to="/">Homepage</router-link></li>
        </ul>
      </nav>
    </div>
    <!-- TV SHOWS LIST -->
    <div class="container">
      <div class="row">
        <div class="twelve columns card" data-aos="fade-up">
          <input id="movie-serach" v-model="search" type="text" placeholder="search shows...">
        </div>
      </div>
      <div v-for="i in Math.ceil(filteredTvShows.length / 4)" class="row">
        <div class="three columns" v-for="show in filteredTvShows.slice((i - 1) * 4, i * 4)">
          <div class="movie-card" data-aos="fade-up">
            <div class="card-image">
              <img :src="show.poster_path" class="u-max-full-width">
            </div>
            <div class="card-content">
              <p class="title">{{ show.title }}</p>
              <p class="card-info">YEAR</p>
              <p class="movie-attribute">{{ show.release_date.split("-")[0] }}</p>
              <p class="card-info">RATING</p>
              <p class="movie-attribute">{{ show.rate.toFixed(2) }} / 10</p>
              <p class="card-info">GENRE</p>
              <p class="movie-attribute"><span :class="{'badge': true, 'comedy-badge': show.genre == 'Komedia' || show.genre == 'Komedia kryminalna', 'action-badge': show.genre == 'Akcja', 'animation-badge': show.genre == 'Animacja', 'action-film-badge': show.genre == 'Sensacyjny', 'surrealistic-badge': show.genre == 'Surrealistyczny', 'fantasy-badge': show.genre == 'Fantasy', 'thriller-badge': show.genre == 'Thriller', 'bio-badge': show.genre == 'Biograficzny', 'crime-badge': show.genre == 'Kryminał', 'drama-badge': show.genre == 'Dramat' || show.genre == 'Dramat obyczajowy'}">{{ show.genre }}</span></p>
              <p class="card-info">DIRECTOR</p>
              <p class="movie-attribute">{{ show.director}}</p>
              <p class="card-info"> EPISODE LENGTH</p>
              <p class="movie-attribute">{{ show.episode_duration.split(".")[0] }}</p>
              <div style="text-align: center;">
                <button class="movie-button" @click="showDetails(show.series_id)">Watch</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Homepage',
  data () {
    return {
      tv_shows: [],
      search: "",

      modal_video: {},
      video_subtitles: false
    }
  },
  mounted() {
    this.getTvShows()
    //this.getTvShows()
    var width = window.matchMedia("(max-width: 480px)")
    this.setDrawer(width)
    width.addListener(this.setDrawer)
    AOS.init({
      duration: 1200
    })

  },
  beforeCreate: function() {
    if (!this.$session.exists()) {
      this.$router.push('/login')
    }
  },
  methods: {
    showDetails: function(id) {
      this.$router.push({name: "TvShow", params: {id}})
    },
    getTvShows: function() {
      this.$http.get("http://192.168.1.148:8000/api/tvseries")
        .then((res) => {
          this.tv_shows = res.body["tv_series"]
        })
        .catch((err) => {
          console.log("err", err)
        })
    },
    logout: function() {
      this.$session.destroy()
      this.$router.push("/")
    },
    setDrawer: function(width) {
      var drawerHTML = document.getElementById("drawer")
      if (width.matches) {
        drawerHTML.innerHTML= "&#9776;";
      } else {
        drawerHTML.innerHTML = "";
      }
      $(".burger-nav").on("click", function(){
        $("nav ul").toggleClass("open");
      });
    }
  },
  computed: {
    filteredTvShows() {
      return this.tv_shows.filter(tv_show => {
        return tv_show.title.toLowerCase().includes(this.search.toLowerCase())
      })
    }
  }
}
</script>

<style scoped>

.image {
  position: relative;
}

.image img {
  width: 100%;
  vertical-align: top;
}

.image:after, .image:before {
  cursor: pointer;
  position: absolute;
  opacity: 0;
  transition: all 0.5s;
  -webkit-transition: all 0.5s;
}

.image:after {
  content: '\A';
  width: 100%; height:100%;
  top: 0; left:0;
  background: rgba(0,0,0,0.7);
}

.image:before {
  font-family: "Roboto", sans-serif;
  font-size: 28px;
  font-weight: 300;
  content: "Show details";
  width: 100%;
  color: #fff;
  z-index: 1;
  bottom: 50%;;
  text-align: center;
  box-sizing: border-box;
  -moz-box-sizing: border-box;
}

.image:hover:after, .image:hover:before {
  opacity: 1;
}

.comedy-badge {
  background-color: #EAF9F2;
  color: #33C37B;
}

.action-badge {
  background: #F2F4FF;
  color: #2F5AF4;
}

.animation-badge {
  background-color: #FFE9EC;
  color: #E5633F;
}

.action-film-badge {
  background-color: #FEEAEB;
  color: #D62A2A;
}

.surrealistic-badge {
  background-color: #EBEBEB;
  color: #737578;
}

.fantasy-badge {
  background-color: #EFECF0;
  color: #67476C;
}

.thriller-badge {
  background-color: #F9DAD4;
  color: #E03616;
}

.bio-badge {
  background-color: #EFF1F0;
  color: #3C5948;
}

.crime-badge {
  background-color: #FEFBF0;
  color: #CCB54C;
}

.drama-badge {
  background-color: #EDEDEC;
  color: #38362C;
}

.badge {
  padding: 7px;
  border-radius: 5px;
}

input {
  margin-bottom: 0px;
}
</style>



