
<template>
  <div class="main">
    <div class="wrapper">
      <nav class="nav">
        <div class="logo">xme<span style="color: #939393;">di</span><span style="color: #bdbdbd;">a</span></div>
        <a class="burger-nav" id="drawer">&#9776;</a>
        <ul>
          <li class="right"><a v-on:click="logout()" href="">Logout</a></li>
          <li class="right"><div class="vertical_hr"></div></li>
          <li class="right"><router-link to="/tvshows">TV shows</router-link></li>
          <li class="right"><router-link class="active" to="/movies">Movies</router-link></li>
          <li class="right"><router-link to="/">Homepage</router-link></li>
        </ul>
      </nav>
    </div>
    <!-- MOVIE LIST -->
    <div class="container">
      <div class="row">
        <div class="twelve columns card" data-aos="fade-up">
          <div class="four columns">
            <img :src="tv_show.poster_path" id="poster" class="u-max-full-width" alt="poster">
          </div>
          <div class="eight columns">
            <h3 id="movie-title">{{ tv_show.title }}</h3>
            <p id="desc">{{ tv_show.description }}</p>
            <div class="five columns">
              <p class="card-info">YEAR</p>
              <p class="movie-attribute" v-if="tv_show.release_date">{{ tv_show.release_date.split("-")[0] }}</p>
              <p class="card-info">RATING</p>
              <p class="movie-attribute" v-if="tv_show.rate">{{ tv_show.rate.toFixed(2) }} / 10</p>
              <p class="card-info">LENGTH</p>
              <p class="movie-attribute" v-if="tv_show">{{ tv_show.episode_duration }}</p>
            </div>
            <div class="five columns">
              <p class="card-info">GENRE</p>
              <p class="movie-attribute" v-if="tv_show">{{ tv_show.genre }}</p>
              <p class="card-info">DIRECTOR</p>
              <p class="movie-attribute" v-if="tv_show">{{ tv_show.director}}</p>
            </div>
          </div>
        </div>
      </div>
      <div class="row">
        <!-- CAST CARD -->
        <div id="cast" class="four columns card" v-if="tv_show.cast" data-aos="fade-up">
          <p id="title">Cast</p>
          <div v-for="c in tv_show.cast.slice(0, maxCast)">
            <img v-if="c.actor_picture_url != '-'" :src="c.actor_picture_url" :alt="c.actor_name">
            <p><span class="actor">{{ c.actor_name }}</span></br><span class="role"> ({{ c.character }})</span></p>
          </div>
          <span id="show-more" @click="maxCast += 3">Show more...</span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Homepage',
  data () {
    return {
      tv_show_id: this.$route.params.id,
      tv_show: {},
      maxCast: 3,
    }
  },
  mounted() {
    this.getTvShowDetails()
    var width = window.matchMedia("(max-width: 480px)")
    this.setDrawer(width)
    width.addListener(this.setDrawer)
    AOS.init({
      duration: 1200
    });
  },
  beforeCreate: function() {
    if (!this.$session.exists()) {
      this.$router.push('/login')
    }
  },
  methods: {
    getTvShowDetails: function() {
      this.$http.get("http://192.168.1.148:8000/api/tvseries/" + this.tv_show_id)
        .then((res) => {
          this.tv_show = res.body
        })
        .catch((err) => {
          console.log("Error", err)
        })
    },
    logout: function() {
      this.$session.destroy()
      this.$router.push("/")
    },
    setDrawer: function(width) {
      var drawerHTML = document.getElementById("drawer")
      if (width.matches) {
        drawerHTML.innerHTML= "&#9776;";
      } else {
        drawerHTML.innerHTML = "";
      }
      $(".burger-nav").on("click", function(){
        $("nav ul").toggleClass("open");
      });
    },

  }
}
</script>

<style scoped>

video {
  border-radius: 5px;
}

#movie-title {
  text-align: center;
  font-family: "Roboto", sans-serif;
}

#poster {
  border-radius: 5px;
  max-height: 400px;
}

#cast {
  text-align: center;
}

#cast img {
  border-radius: 5px;
  height: 100px;
}

#title {
  font-weight: 500;
  font-size: 32px;
}

.actor {
  color: #1C2329;
  font-weight: 500;
}

.role {
  color: #8E8E8E;
}

#show-more {
  color: #8E8E8E;
  cursor: pointer;
}

.btn-grad {
  margin-top: 20px;
  color: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(101deg, #78e4ff, #ff48fa);
  border: 2px solid transparent;
  padding-bottom: 37px;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(101deg, #78e4ff, #ff48fa);
  background-origin: border-box;
  background-clip: content-box, border-box;
  box-shadow: 2px 1000px 2px #fff inset;
  transition: 0.4s;
}

.btn-grad span{
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), -webkit-linear-gradient(101deg, #78e4ff, #ff48fa);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

.btn-grad:hover {
  box-shadow: none;
}

.active-sub {
  margin-top: 20px;
  border: 2px solid transparent;
  padding-bottom: 37px;
  background-image: linear-gradient(rgba(255, 255, 255, 0), rgba(255, 255, 255, 0)), linear-gradient(101deg, #78e4ff, #ff48fa);
  background-origin: border-box;
  background-clip: content-box, border-box;
}

.active-sub span, .btn-grad:hover span {
  background-color: #fff;
  -webkit-background-clip: text;
  -webkit-text-fill-color: #fff;
}

.center {
  text-align: center;
}
</style>



